import React, { useState, useEffect } from 'react';
import { useHistory } from 'react-router-dom';
import { CustomInput, Row,Label,Input } from 'reactstrap';
import { Colxx, Separator } from '../../../../components/common/CustomBootstrap';
import StateButton from '../../../../components/StateButton';
import IntlMessages from '../../../../helpers/IntlMessages';
import instance from '../../../../helpers/axiosInstance';
import { adminRoot } from '../../../../constants/defaultValues';
import { getProfileData } from '../../../../helpers/Utils';
// import AnswerCard from '../../../../components/cards/AnswerCard';

const StepFour = ({setCompletePercentage, hackData}) => {
  const history = useHistory();
  const [conditionsAccepted, setAccepted] = useState(false);
  const [responseData,setResponseData] = useState([]);
  const profileData = getProfileData();
  let count=0;
  const [values,setValues] = useState({
    answer1:'',
    answer2:'',
    answer3:'',
    answer4:'',
    answer5:'',
    answer6:'',
    answer7:'',
    answer8:'',
    answer9:'',
    answer10:'',
  });

  useEffect(() => {
    if(conditionsAccepted){
      setCompletePercentage(100);
    }
  }, [conditionsAccepted]);

  const handleChange = (event, key) => {
    setValues({
      ...values,
      [key]: event.target.value,
    });
  };


  const handleSuccessButtonClick = async() => {

    hackData.custom_questions.forEach((item,index) => {
      let ans;
      if(index===0){
        ans=values.answer1;
      }
      if(index===1){
        ans=values.answer2;
      }
      if(index===2){
        ans=values.answer3;
      }
      if(index===3){
        ans=values.answer4;
      }
      if(index===4){
        ans=values.answer5;
      }
      if(index===5){
        ans=values.answer6;
      }
      if(index===6){
        ans=values.answer7;
      }
      if(index===7){
        ans=values.answer8;
      }
      if(index===8){
        ans=values.answer9;
      }if(index===0){
        ans=values.answer1;
      }
      if(index===1){
        ans=values.answer2;
      }
      if(index===2){
        ans=values.answer3;
      }
      if(index===3){
        ans=values.answer4;
      }
      if(index===4){
        ans=values.answer5;
      }
      if(index===9){
        ans=values.answer10;
      }
      const temp = {'question_id': item.id,
      'question': item.customized_questions,
      'answer': ans};
      console.log(ans);
      const spar = responseData;
      spar.push(temp);
      setResponseData(spar);
    })

    if (conditionsAccepted) {
        const response = await instance({
          method: 'POST',
          url: '/addparticipant',
          data: { user_id:profileData.user_id,hackathon_id:hackData.hackathon.id, status:"0", responses:responseData },
        });
    
        const { data } = response;
    
        if (data.success) {
          history.push(`${adminRoot}/challenges/${hackData.hackathon.id}`)
          return new Promise((resolve) => {
            resolve('Hackathon applied successfully!');
          });  
        }
          return new Promise((_, reject) => {
            reject(data.message);
          });
        
      } 
        return new Promise((_, reject) => {
          reject('Please accept terms and conditions!');
        });
      
  };

  return (
    <div className="wizard-basic-step m-4 pt-4">
      <h6 className="lead">
        <IntlMessages id="challenges.terms-and-conditions-title" />
      </h6>
      <p>
        <IntlMessages id="challenges.terms-and-conditions-text" />
      </p>

      <CustomInput
        type="checkbox"
        label={<IntlMessages id="challenges.terms-and-conditions" />}
        checked={conditionsAccepted}
        onClick={() => setAccepted(!conditionsAccepted)}
      />

        {hackData.custom_questions.map((item) => {
          if (item.customized_questions !== undefined && count === 0) {
            count = 1;
            return (

              <Row>
                <Colxx xxs="12">
                <Separator className="mb-5" />
                  <h1>Answer Important Questions</h1>
                  <Separator className="mb-5" />
                </Colxx>
              </Row>
            );
          }
          return null;
        })}

          {hackData.custom_questions.map((item,index) => (
               <>
                  <Label key={item.id}>
                    <IntlMessages id={item.customized_questions} />
                  </Label>

                  <Input
                    id={item.id}
                    type="text"
                    label={<IntlMessages id="enter your answer" />}
                    placeholder='enter your answer here'
                    onChange={(event) => handleChange(event,`answer${index+1}`)}
                  />
                  <br />
              </>
          ))}

      <Colxx xxs="12" className="text-center my-4">
        <StateButton
          id="step-one-button"
          color="primary"
          onClick={handleSuccessButtonClick}
        >
          <IntlMessages id="forms.submit" />
        </StateButton>
      </Colxx>
    </div>
  );
};

export default StepFour;
