/* eslint-disable camelcase */
import React, { useState } from 'react';
import { useHistory } from 'react-router-dom';
import { Button, Card, CardBody, CardTitle, Modal } from 'reactstrap';
import JoinTeamModal from '../../../../components/application/JoinTeamModal';

import { Separator } from '../../../../components/common/CustomBootstrap';
import StateButton from '../../../../components/StateButton';
import instance from '../../../../helpers/axiosInstance';
import { adminRoot } from '../../../../constants/defaultValues';
import IntlMessages from '../../../../helpers/IntlMessages';
import { getProfileData } from '../../../../helpers/Utils';

const CompleteStatus = ({ completePercentage, id }) => {
  const history = useHistory();
  const [openJoinTeam, setOpenJoinTeam] = useState(false);

  const { user_id } = getProfileData();

  const handleSubmit = async () => {
    return new Promise((success, failed) => {
      if (completePercentage < 100) {
        failed('Please Complete the Form!');
      }

      const addJudge = async () => {
        console.log("hackathon Submitted");
        try {
          const response = await instance({
            method: 'POST',
            url: '/addparticipant',
            data: {
              hackathon_id: id,
              status: '0',
              user_id,
            },
          });

          const { data } = response;
          if (data.success) {
            success('Application submitted successfully!');
            history.push(`${adminRoot}/challenges/${id}`);
          } else {
            failed('Failed to Submit Application!');
          }
        } catch (error) {
          console.log(error);
          failed('Failed to update hackathon!');
        }
      };

      addJudge();
    });
  };

  return (
    <>
      <Card>
        <CardBody className="text-center">
          <CardTitle>
            <h2 className="lead">
              <IntlMessages id="challenges.application-not-submitted" />
            </h2>
            <Separator className="mb-5" />
          </CardTitle>
          <p>
            <IntlMessages id="challenges.application-filled" />
          </p>
          <h3 className="lead">{completePercentage}%</h3>
          <p>
            <IntlMessages id="challenges.fill-complete-message" />
          </p>

          <StateButton
            id="submit-application"
            color="primary"
            onClick={handleSubmit}
            className={completePercentage >= 100 ? '' : 'disabled'}
            successNext={() => {
              window.location.href = '/';
            }}
          >
            <IntlMessages id="challenges.submit-individual-button" />
          </StateButton>

          <Separator className="mt-4 mb-4" />

          <Button outline onClick={() => setOpenJoinTeam(true)}>
            <IntlMessages id="challenges.join-team-button" />
          </Button>

          <Modal
            isOpen={openJoinTeam}
            toggle={() => setOpenJoinTeam(!openJoinTeam)}
          >
            <JoinTeamModal setOpenModal={setOpenJoinTeam} />
          </Modal>
        </CardBody>
      </Card>
    </>
  );
};

export default CompleteStatus;
