import React, { useState } from 'react';
import {
  Button,
  FormGroup,
  Input,
  Label,
  ModalBody,
  ModalFooter,
  ModalHeader,
} from 'reactstrap';
// import instance from '../../helpers/axiosInstance';
import IntlMessages from '../../helpers/IntlMessages';

import StateButton from '../StateButton';

const JoinTeamModal = ({ setOpenModal }) => {
  const [formValues, setFormValues] = useState({
    code: '',
  });

  const handleSuccessButtonClick = () => {
    return new Promise((success) => {
      setTimeout(() => {
        success('Everything went right!');
      }, 1400);
    });
  };

  // const handleSubmit = async (newData) => {
  //   return new Promise((success, failed) => {
  //     const updateProfile = async () => {
  //       try {
  //         const response = await instance({
  //           method: 'POST',
  //           url: '/addquestion',
  //           data: JSON.stringify({ question: [{ ...newData }] }),
  //           // headers: {
  //           //   Authorization: `Bearer ${access_token}`,
  //           // },
  //         });

  //         const { data } = response;
  //         console.log(data);
  //         if (data.success) {
  //           setFormValues({ ...formValues, ...newData });
  //           success('Hackathon updated successfully!');
  //         } else {
  //           failed('Failed to update hackathon!');
  //         }
  //       } catch (error) {
  //         console.log(error);
  //         failed('Failed to update hackathon!');
  //       }
  //     };
  //     updateProfile();
  //   });
  // };

  const handleChange = (event, key, value = false) => {
    setFormValues({
      ...formValues,
      [key]: value ? event : event.target.value,
    });
  };

  return (
    <>
      <ModalHeader>
        <h2 className="lead">
          <IntlMessages id="challenges.modal.join-team-title" />
        </h2>
      </ModalHeader>

      <ModalBody>
        <FormGroup>
          <Label>
            <IntlMessages id="challenges.modal.code" />
          </Label>
          <FormGroup>
            <Input
              type="text"
              value={formValues.question}
              onChange={(event) => handleChange(event, 'code')}
            />
          </FormGroup>
        </FormGroup>
      </ModalBody>

      <ModalFooter>
        <StateButton
          id="add-question-buttom"
          color="secondary"
          onClick={() => handleSuccessButtonClick(formValues)}
        >
          <IntlMessages id="challenges.modal.application-add" />
        </StateButton>
        <Button color="danger" onClick={() => setOpenModal(false)}>
          <IntlMessages id="challenges.modal.application-cancel" />
        </Button>
      </ModalFooter>
    </>
  );
};

export default JoinTeamModal;
