import React, {useState} from 'react';
import { connect } from 'react-redux';
import {
  Row,
 // Button,
  Card,
  CardBody,
  CustomInput,
  CardTitle,
  FormGroup,
  Input,
  InputGroup,
  InputGroupAddon,
  Label,
} from 'reactstrap';
import Select from 'react-select';
import DatePicker from 'react-datepicker';
// import { useHistory } from 'react-router-dom';
import {
  Colxx,
  Separator,
} from '../../../../components/common/CustomBootstrap';
import StateButton from '../../../../components/StateButton';
import { NotificationManager } from '../../../../components/common/react-notifications';
import CustomSelectInput from '../../../../components/common/CustomSelectInput';
import {
 // adminRoot,
  genderOptions,
  shirtSizeOptions,
} from '../../../../constants/defaultValues';
import instance from '../../../../helpers/axiosInstance';
import { setProfileData,getProfileData } from '../../../../helpers/Utils';
// import StepTwo from './step-two';
// import { hackthonApplication } from '../../../../constants/defaultValues';
import IntlMessages from '../../../../helpers/IntlMessages';
// import Section from './components/section';

const addLeadingZero = (input) => {
  return input < 10 ? `0${input}` : input;
};

const StepOne = ({ hackData, currentUser, setChange, setCompletePercentage }) => {
// const history = useHistory();
  const { hackathon } = hackData;
  const { email } = currentUser;
  const profileData = getProfileData();
  console.log(email);
  const { id } = profileData;
  const id2 = profileData.user_id;
//  const [editAbout, setEditAbout] = useState(false);
  const [noFormalEducation, setNoFormalEducation] = useState(false);
  const [currentlyStudyHere, setCurrentlyStudyHere] = useState(false);
  const [noExperience, setNoExperience] = useState(false);

  const [formValues, setFormValues] = useState({
    full_name : profileData.full_name,
    gender : profileData.gender,
    tshirt_size : profileData.tshirt_size,
    contact_no : profileData.contact_no,
    bio : profileData.bio,

    degree_type : profileData.degree_type,
    education_institution : profileData.education_institution,
    field_of_study : profileData.field_of_study,
    month_of_graduation: profileData.month_of_graduation || new Date().getMonth(),
    year_of_graduation: profileData.year_of_graduation || new Date().getFullYear(),

    employer: profileData.employer,
    role: profileData.role,
    joining_data: profileData.joining_data,
    leaving_data: profileData.leaving_data,
    job_description: profileData.job_description,

    facebook: profileData.facebook,
    github: profileData.github,
    linkedin: profileData.linkedin,
    instagram: profileData.instagram,

    city: profileData.city,
  });

  const [startDate, setStartDate] = useState(
    new Date(formValues.year_of_graduation, formValues.month_of_graduation)
  ); 

  const handleChange = (event, key) => {
    setFormValues({
      ...formValues,
      [key]: event.target.value,
    });
  };

  const handleSubmitAbout = async (newData) => {

    if (
      newData.full_name === null ||
      newData.gender === null ||
      newData.tshirt_size === null ||
      newData.contact_no === null ||
      newData.bio === null ||
      newData.linkedin === null ||
      newData.city === null
      ) 
    {
      NotificationManager.error(
        'Please enter all the information!',
        'Hackathon Operation Error',
        3000,
        null,
        null,
        ''
      );
    }
    else{
      const response = await instance({
        method: 'POST',
        url: '/updateProfile',
        data: { user_id:id2,id, ...newData },
      });
  
      const { data } = response;
  
      if (data.success) {
        setProfileData({ ...profileData, ...newData });
        setCompletePercentage(75);
        setChange(2);
        
        return new Promise((resolve) => {
          resolve('Profile updated successfully!');
        }); 
        
      }
    }

    return new Promise((_, reject) => {
      // eslint-disable-next-line
      reject('Failed to update profile!');
    });
  };

  // const applicationData = JSON.parse(hackathon.application) || {
  //   about: [],
  //   contact: [],
  //   education: [],
  //   experience: [],
  //   online_profiles: [],
  // };

  // const applicationAboutData = hackthonApplication.about.filter(({ value }) =>
  //   applicationData.about.includes(value)
  // );
  // const applicationContactData = hackthonApplication.contact.filter(
  //   ({ value }) => applicationData.contact.includes(value)
  // );
  // const applicationEducationData = hackthonApplication.education.filter(
  //   ({ value }) => applicationData.education.includes(value)
  // );
  // const applicationExpericeData = hackthonApplication.experience.filter(
  //   ({ value }) => applicationData.experience.includes(value)
  // );
  // const applicationOnlineProfileData = hackthonApplication.online_profiles.filter(
  //   ({ value }) => applicationData.online_profiles.includes(value)
  // );

  // const handleSuccessButtonClick = () => {
  //   return new Promise((success) => {
  //     setTimeout(() => {
  //       success('Everything went right!');
  //     }, 1400);
  //   });
  // };

  return (
    <div className="wizard-basic-step m-4 pt-4">
      <Card className="mb-4" color="secondary">
        <CardBody>
          <CardTitle className="d-flex justify-content-between">
            <IntlMessages id="challenges.step-1" />
          </CardTitle>
        </CardBody>
      </Card>
      <h2 className="lead">{hackathon.name}</h2>
      <p>
        <IntlMessages id="challenges.sub-title" />
      </p>
      <Separator className="mb-2" />

      {/* {applicationAboutData.length ? (
        <Section data={applicationAboutData} userData={userData} />
      ) : null}

      {applicationExpericeData.length ? (
        <Section data={applicationExpericeData} userData={userData} />
      ) : null}

      {applicationEducationData.length ? (
        <Section data={applicationEducationData} userData={userData} />
      ) : null}

      {applicationOnlineProfileData.length ? (
        <Section data={applicationOnlineProfileData} userData={userData} />
      ) : null}

      {applicationContactData.length ? (
        <Section data={applicationContactData} userData={userData} />
      ) : null} */}

<Row className="justify-content-center">
        <Colxx xxs="12" md="12">
          <Card className="mb-4">
            {/* <div className="position-absolute card-top-buttons">
              <Button
                outline
                color="primary"
                onClick={() => {
                  setEditAbout(!editAbout);
                }}
                className="icon-button"
              >
                <i className="simple-icon-pencil" />
              </Button>
            </div> */}
            <CardBody>
              <CardTitle>
                <h2 className="lead">
                  <IntlMessages id="Update your all details" />
                </h2>
              </CardTitle>

              <Separator className="mb-3" />

              <FormGroup className="mb-3">
                <Label>
                  <IntlMessages id="forms.full-name" />
                </Label>
                <Input
                  type="text"
                  name="full-name"
                  value={formValues.full_name}
                  onChange={(event) => {
                    handleChange(event, 'full_name');
                  }}
                  
                />
              </FormGroup>

              <FormGroup className="mb-3">
                <Label>
                  <IntlMessages id="forms.email" />
                </Label>
                <Input type="text" name="email" value={email} disabled />
              </FormGroup>

              <FormGroup className="mb-3">
                <Label>
                  <IntlMessages id="forms.phone" />
                </Label>
                <Input
                  type="text"
                  name="phone"
                  value={formValues.contact_no}
                  onChange={(event) => {
                    handleChange(event, 'contact_no');
                  }}
                  
                />
              </FormGroup>

              <FormGroup className="mb-3">
                <Label>
                  <IntlMessages id="forms.gender" />
                </Label>
                
                  <Select
                    components={{ Input: CustomSelectInput }}
                    className="react-select"
                    classNamePrefix="react-select"
                    name="select-gender"
                    defaultInputValue={formValues.gender}
                    onChange={(selectedOption) => {
                      setFormValues({
                        ...formValues,
                        gender: selectedOption.value,
                      });
                    }}
                    options={genderOptions}
                  />
                {/* ) : (
                  <Input
                    type="text"
                    name="gender"
                    value={formValues.gender}
                    
                  />
                )} */}
              </FormGroup>

              <FormGroup className="mb-3">
                <Label>
                  <IntlMessages id="forms.t-shirt-size" />
                </Label>
                
                  <Select
                    components={{ Input: CustomSelectInput }}
                    className="react-select"
                    classNamePrefix="react-select"
                    name="select-t-shirt-size"
                    defaultInputValue={formValues.tshirt_size}
                    onChange={(selectedOption) => {
                      setFormValues({
                        ...formValues,
                        tshirt_size: selectedOption.value,
                      });
                    }}
                    options={shirtSizeOptions}
                  />
                {/* ) : (
                  <Input
                    type="text"
                    name="t-shirt-size"
                    value={formValues.tshirt_size}
                    
                  />
                )} */}
              </FormGroup>

              <FormGroup className="mb-3">
                <Label>
                  <IntlMessages id="forms.bio" />
                </Label>
                <Input
                  type="textarea"
                  name="bio"
                  rows="6"
                  placeholder="About Yourself"
                  value={formValues.bio}
                  onChange={(event) => {
                    handleChange(event, 'bio');
                  }}
                  
                />
              </FormGroup>

              <FormGroup>
                <CustomInput
                  type="checkbox"
                  id="formal-education"
                  checked={noFormalEducation}
                  onChange={() => {
                    setNoFormalEducation(!noFormalEducation);
                    if (noFormalEducation) {
                      setFormValues({
                        ...formValues,
                        degree_type: '',
                        education_institution: '',
                      });
                    }
                  }}
                  
                  label="I don't have Formal Education."
                />
              </FormGroup>

              <FormGroup className="mb-3">
                <Label>
                  <IntlMessages id="forms.degree-type" />
                </Label>
                <Input
                  type="text"
                  name="degree-type"
                  placeholder="e.g. B. Tech in Computer Science"
                  value={formValues.degree_type}
                  onChange={(event) => {
                    handleChange(event, 'degree_type');
                  }}
                  disabled={noFormalEducation}
                />
              </FormGroup>

              <FormGroup className="mb-5">
                <Label>
                  <IntlMessages id="forms.select-institution" />
                </Label>
                <Input
                  type="text"
                  name="select-institution"
                  placeholder="e.g Vellore Institute of Technology"
                  value={formValues.education_institution}
                  onChange={(event) => {
                    handleChange(event, 'education_institution');
                  }}
                  disabled={noFormalEducation}
                />
              </FormGroup>

              <Separator className="mt-2 mb-2" />

              <FormGroup>
                <CustomInput
                  type="checkbox"
                  id="curently-study-here"
                  checked={currentlyStudyHere}
                  onChange={() => {
                    setCurrentlyStudyHere(!currentlyStudyHere);
                    if (currentlyStudyHere) {
                      setFormValues({
                        ...formValues,
                        month_of_graduation: '',
                        year_of_graduation: '',
                      });
                    }
                  }}
                  
                  label="I currently study here."
                />
              </FormGroup>

              <FormGroup className="mb-3">
                <Label>
                  <IntlMessages id="forms.field-of-study" />
                </Label>
                <Input
                  type="text"
                  name="field-of-study"
                  placeholder="e.g. Software Engineering"
                  value={formValues.field_of_study}
                  onChange={(event) => {
                    handleChange(event, 'field_of_study');
                  }}
                  disabled={currentlyStudyHere}
                />
              </FormGroup>

              <Row>
                <Colxx>
                  <FormGroup className="mb-3">
                    <Label>
                      <IntlMessages id="forms.month-of-graduation" />
                    </Label>
                    {!currentlyStudyHere ? (
                      <DatePicker
                        selected={startDate}
                        onChange={(date) => {
                          setStartDate(date);
                          const event = {
                            target: {
                              value: addLeadingZero(date.getMonth() + 1),
                            },
                          };
                          handleChange(event, 'month_of_graduation');
                        }}
                        dateFormat="MM"
                        showMonthYearPicker
                      />
                    ) : (
                      <Input
                        type="text"
                        name="month"
                        placeholder={`e.g. ${new Date().getMonth()}`}
                        value={formValues.month_of_graduation}
                        disabled={currentlyStudyHere}
                      />
                    )}
                  </FormGroup>
                </Colxx>

                <Colxx>
                  <FormGroup className="mb-3">
                    <Label>
                      <IntlMessages id="forms.year-of-graduation" />
                    </Label>

                    {!currentlyStudyHere ? (
                      <DatePicker
                        selected={startDate}
                        onChange={(date) => {
                          setStartDate(date);
                          const event = {
                            target: {
                              value: JSON.stringify(date.getFullYear()),
                            },
                          };
                          handleChange(event, 'year_of_graduation');
                        }}
                        showYearPicker
                        dateFormat="yyyy"
                      />
                    ) : (
                      <Input
                        type="text"
                        name="year"
                        placeholder={`e.g. ${new Date().getFullYear()}`}
                        value={formValues.year_of_graduation}
                        disabled={currentlyStudyHere}
                      />
                    )}
                  </FormGroup>
                </Colxx>

              </Row>

              <FormGroup>
                <CustomInput
                  type="checkbox"
                  id="formal-education"
                  
                  label="I am yet to find opportunity."
                  checked={noExperience}
                  onChange={() => setNoExperience(!noExperience)}
                />
              </FormGroup>

              <Row>
                <Colxx md="6">
                  <FormGroup className="mb-3">
                    <Label>
                      <IntlMessages id="forms.employer" />
                    </Label>
                    <Input
                      type="text"
                      name="employer"
                      placeholder="employer"
                      value={formValues.employer}
                      onChange={(event) => {
                        handleChange(event, 'employer');
                      }}
                      disabled={noExperience}
                    />
                  </FormGroup>
                </Colxx>
                <Colxx md="6">
                  <FormGroup className="mb-3">
                    <Label>
                      <IntlMessages id="forms.role" />
                    </Label>
                    <Input
                      type="text"
                      name="role"
                      placeholder="Role"
                      value={formValues.role}
                      onChange={(event) => {
                        handleChange(event, 'role');
                      }}
                      disabled={noExperience}
                    />
                  </FormGroup>
                </Colxx>
              </Row>

              <Row>
                <Colxx md="6">
                  <FormGroup className="mb-3">
                    <Label>
                      <IntlMessages id="forms.from" />
                    </Label>
                    <Input
                      type="text"
                      name="joining_data"
                      placeholder="2021"
                      value={formValues.joining_data}
                      onChange={(event) => {
                        handleChange(event, 'joining_data');
                      }}
                      disabled={noExperience}
                    />
                  </FormGroup>
                </Colxx>

                <Colxx md="6">
                  <FormGroup className="mb-3">
                    <Label>
                      <IntlMessages id="forms.to" />
                    </Label>
                    <Input
                      type="text"
                      name="leaving_data"
                      placeholder="04"
                      value={formValues.leaving_data}
                      onChange={(event) => {
                        handleChange(event, 'leaving_data');
                      }}
                      disabled={noExperience}
                    />
                  </FormGroup>
                </Colxx>
              </Row>

              <FormGroup className="mb-3">
                <Label>
                  <IntlMessages id="forms.description" />
                </Label>
                <Input
                  type="textarea"
                  name="job_description"
                  placeholder="Desribe your work experience"
                      value={formValues.job_description}
                      onChange={(event) => {
                        handleChange(event, 'job_description');
                      }}
                  rows="6"
                  disabled={noExperience}
                />
              </FormGroup>
              <FormGroup className="mb-6">
                <InputGroup>
                  <InputGroupAddon addonType="prepend">
                    <i className="simple-icon-social-linkedin btn" />
                  </InputGroupAddon>
                  <Input
                    type="text"
                    className="form-control"
                    name="linkedin"
                    value={formValues.linkedin}
                    onChange={(event) => {
                      handleChange(event, 'linkedin');
                    }}
                    
                  />
                </InputGroup>
              </FormGroup>

              <FormGroup className="mb-6">
                <InputGroup>
                  <InputGroupAddon addonType="prepend">
                    <i className="simple-icon-social-facebook btn" />
                  </InputGroupAddon>
                  <Input
                    type="text"
                    name="facebook"
                    value={formValues.facebook}
                    onChange={(event) => {
                      handleChange(event, 'facebook');
                    }}
                    
                  />
                </InputGroup>
              </FormGroup>

              <FormGroup className="mb-6">
                <InputGroup>
                  <InputGroupAddon addonType="prepend">
                    <i className="simple-icon-social-github btn" />
                  </InputGroupAddon>
                  <Input
                    type="text"
                    name="github"
                    className="form-control"
                    value={formValues.github}
                    onChange={(event) => {
                      handleChange(event, 'github');
                    }}
                    
                  />
                </InputGroup>
              </FormGroup>

              <FormGroup className="mb-6">
                <InputGroup>
                  <InputGroupAddon addonType="prepend">
                    <i className="simple-icon-social-instagram btn" />
                  </InputGroupAddon>
                  <Input
                    type="text"
                    name="github"
                    className="form-control"
                    value={formValues.instagram}
                    onChange={(event) => {
                      handleChange(event, 'instagram');
                    }}
                    
                  />
                </InputGroup>
              </FormGroup>

              <FormGroup className="mb-3">
          <Label>
            <IntlMessages id="forms.email" />
          </Label>
          <Input
            type="text"
            name="email"
            value={email}
            disabled
            title="Can't update Email"
          />
        </FormGroup>

        <Separator className="mb-4" />

        <FormGroup className="mb-3">
          <Label>
            <IntlMessages id="forms.phone" />
          </Label>
          <Input
            type="text"
            name="contact-number"
            value={formValues.contact_no}
            onChange={(event) => {
              handleChange(event, 'contact_no');
            }}
            
          />
        </FormGroup>

        <FormGroup className="mb-3">
          <Label>
            <IntlMessages id="forms.city" />
          </Label>
          <Input
            type="text"
            name="city"
            placeholder="e.g. Delhi"
            value={formValues.city}
            onChange={(event) => {
              handleChange(event, 'city');
            }}
            
          />
        </FormGroup>


             
                <FormGroup className="text-center">
                  <StateButton
                    id="submitAndNextButton"
                    color="primary"
                    onClick={() => handleSubmitAbout(formValues)}
                    // next={() => {
                    //   setEditAbout(false);
                    // }}
                  >
                    <IntlMessages id="forms.submit" />
                  </StateButton>
                </FormGroup>
           
            </CardBody>
          </Card>
        </Colxx>
      </Row>
      {/* <CustomInput
        type="checkbox"
        label={<IntlMessages id="challenges.terms-and-conditions" />}
      /> */}
      {/* <Colxx xxs="12" className="text-center my-4">
        <StateButton
          id="step-one-button"
          color="primary"
          onClick={handleSuccessButtonClick}
        >
          <IntlMessages id="forms.submit" />
        </StateButton>
      </Colxx> */}
    </div>
  );
};
const mapStateToProps = ({ history, authUser }) => {
  const { currentUser, profileData } = authUser;
  return { currentUser, profileData, history };
};

const mapActionsToProps = {};

export default connect(mapStateToProps, mapActionsToProps)(StepOne);
